.bill {
   padding-top: 1em;   
   display: flex;
   justify-content: space-around;  
    
}
.billetterie {
 
   justify-content: space-around;
   min-height: 100vh;
   
   background-size: cover;
   background-color: var(--gris);
   
}
.card-name {
   font-family: katibeh, Arial, Helvetica, sans-serif;
   font-size: 3em;
   padding-bottom: 0.5em;
}

.card-fond {
   background-color: var(--gris);
   max-width: 15em;
   height: auto;
   
}

.vignetteBillet {
   text-align: center;
   margin-top: 2em;
   margin-bottom: 2em;
}

.vignette {
   display: grid;
   justify-content: space-around;
}